import React from 'react'
import branchIcon from '../../img/Branches.png'
import icon1 from "../../img/brand.png";
import icon2 from "../../img/value.png";
import icon3 from "../../img/accolades.png";

const Counter = class extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            active: false
        }
    }

    render() {
        const {data} = this.props;
        if (!data){
            return <></>;
        }

        return (
            <section className="counter py-5" style={{background: 'linear-gradient(45deg,#282828, #282828d9)'}}>
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <div className="heading-container">
                                <div className="heading">{data.heading}</div>
                                <p>{data.caption}</p>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        {
                            data.counterBox.map((item) => {
                                return (
                                    <div className="col-lg-3 col-6 mb-5 mb-sm-0 text-lg-left">
                                        <div className="counter-box d-sm-inline-flex align-items-center">
                                            <figure class={'mb-0 mr-sm-3 mb-3 mb-sm-0'}>
                                                <img src={branchIcon} alt="" className={'counter-icon'} />
                                            </figure>
                                            <div>
                                                <div className="counter-heading">{item.counterBoxTitle}</div>
                                                <span>{item.counterBoxContent}</span>
                                            </div>
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
            </section>
        )
    }


}

const CounterCard = (props) => {
    let icon;
    if(props.index == 0) {
        icon = icon1;
    }
    else if (props.index == 1) {
        icon = icon2;
    }
    else if (props.index == 2) {
        icon = icon3;
    }
    return (
        <div
            className={`about-box p-3 position-absolute animate__animated animate__slow ${props.aboutCardsAnimationStarted ? 'animate__fadeInUp' : ''}`}
            style={{top: 0, left: 0, animationDelay: `${(props.index) * (.1)}s`}}>
            <div className={'about-box-icon'}>
                <img src={icon} alt="" style={{maxWidth: '50px'}} />
            </div>
            <div className="about-box-title">{!!props.item.aboutCardTitle ? props.item.aboutCardTitle : ''}</div>
            <div className="description">
                {!!props.item.aboutCardContent ? props.item.aboutCardContent : ''}
            </div>
        </div>
    );
};

export default Counter
